export default class PraiseButton {
  
  constructor (props = {
    selector: 'body',
    times: 0,
    onTimesChange: () => {}
  }) {

    this.el = document.querySelector(props.selector)
    this.times = props.times || 0
    this.onTimesChange = props.onTimesChange || this._onTimesChange
    this.onTimesChange(this.times)
  }

  add () {

    this.times++
    this.onTimesChange(this.times)
  }

  _onTimesChange () {}
}
